@import "styles/globalVariables.css";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: var(--tg-theme-bg-color);
    color: var(--tg-theme-text-color);
}
a {
    text-decoration: none;
    color: inherit;
}

.container{
    height: 100%;
}
.wrapper_with_header{
    margin: 0 auto;
    padding-top: calc(var(--header-height) + 1vh);
}
.wrapper_dialog{
    margin: 0 auto;
    max-width: var(--mobile-width);
}